    <div class="b_t" v-for="(item,index) in record.slice(4,8)" :key="index">
      <div class="b_t_top">
        <span>{{item.name}}</span>
        <span class="MORE" :goList="item" @click="goTo(item.name,item.list)">MORE<van-icon name="play" /></span>
      </div>
      <div class="bottom">
          <div class="text_list">
            <div
              class="list_content"
              v-for="sub in item.list"
