    display: flex;
    flex-wrap: wrap;
    padding: 6px;
    overflow: hidden;
    .list_content {
      display: flex;
      width: 95%;
      align-items: center;
      padding: 10px;
      border-bottom: 1px solid rgb(233, 233, 233);

      .text_time {
        flex: 1;
        display: block;
